body {
  font-family: 'Universal Sans Display', -apple-system, Arial, sans-serif;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}


.dashboard {
  width: 1000px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 30px;
}

.table {
  width: 600px;
  text-align: left;
}



.container {
  position: relative;
  border: 1px solid black;
  height: 800px;
  width: 1000px;
  overflow: hidden;
  background-image: 
    linear-gradient(to right, black 1px, transparent 1px),
    linear-gradient(to bottom, black 1px, transparent 1px);
  background-size: 100px 100px;
}

.box {
  position: absolute;
  top: 0;
  left: 0;
  background-color: hotpink;
  height: 100px;
  width: 400px;
  cursor: pointer;
  /* background-image: url('./assets/megapack_xl.png');
  background-size: contain;
  background-position: center;
background-repeat: no-repeat; */
}



.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  padding: 0 20px;

}

.logo {
  width: 140px;
  height: 32px;
}



.language {
  height: 34px;
  width: 72px;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.total {
  display: flex;
}


.quantity {
  display: flex;
  flex-direction: column;
}


.mpxl {
  width: 400px;
}

.mp2 {
  width: 300px;
}

.mp {
  width: 300px;
}

.pp {
  width: 100px;
}